//
// Mixins
//

@mixin code-type() {
  font-family: 'Monaco', 'Courier';
  font-size: rem(13);
  line-height: rem(24);
  white-space: pre-wrap;
  word-break: break-word;
  word-spacing: normal;
}

//
// Vaud – Plaid's official typeface
//

@font-face {
  font-family: 'Vaud';
  font-style: normal;
  font-weight: 700;
  src: url('data:application/font-woff2;charset=utf-8;base64,') format('woff2'), url('data:application/font-woff;charset=utf-8;base64,') format('woff');
}

@font-face {
  font-family: 'Vaud';
  font-style: italic;
  font-weight: 700;
  src: url('data:application/font-woff2;charset=utf-8;base64,') format('woff2'), url('data:application/font-woff;charset=utf-8;base64,') format('woff');
}

@font-face {
  font-family: 'Vaud';
  font-style: normal;
  font-weight: 500;
  src: url('data:application/font-woff2;charset=utf-8;base64,') format('woff2'), url('data:application/font-woff;charset=utf-8;base64,') format('woff');
}


@font-face {
  font-family: 'Vaud';
  font-style: italic;
  font-weight: 500;
  src: url('data:application/font-woff2;charset=utf-8;base64,') format('woff2'), url('data:application/font-woff;charset=utf-8;base64,') format('woff');
}

@font-face {
  font-family: 'Vaud';
  font-style: normal;
  font-weight: 400;
  src: url('data:application/font-woff2;charset=utf-8;base64,') format('woff2'), url('data:application/font-woff;charset=utf-8;base64,') format('woff');
}

@font-face {
  font-family: 'Vaud';
  font-style: italic;
  font-weight: 400;
  src: url('data:application/font-woff2;charset=utf-8;base64,') format('woff2'), url('data:application/font-woff;charset=utf-8;base64,') format('woff');
}

@font-face {
  font-family: 'Vaud';
  font-style: normal;
  font-weight: 200;
  src: url('data:application/font-woff2;charset=utf-8;base64,') format('woff2'), url('data:application/font-woff;charset=utf-8;base64,') format('woff');
}


//
// Primary headings
//

h1,
.primary-heading {
  color: $variables-primary-blue-dark;
  font-size: 3.2rem;
  font-weight: 400;
  line-height: 4rem;
  margin-bottom: rem($variables-spacing-medium);

  &--is-centered {
    text-align: center;
  }

  &--is-bright {
    color: $variables-white;
    text-shadow: 0 1px 3px $variables-primary-text-shadow-color;
  }

  &--is-muted {
    color: $variables-gray-dark;
  }
}

//
// Secondary headings
//

h2,
.secondary-heading {
  color: $variables-primary-blue-dark;
  font-size: 2.4rem;
  font-weight: 500;
  line-height: 3.2rem;
  margin-bottom: rem($variables-spacing-medium);

  &--is-centered {
    text-align: center;
  }

  &--is-bright {
    color: $variables-white;
    text-shadow: 0 1px 2px $variables-primary-text-shadow-color;
  }

  &--is-muted {
    color: $variables-gray-dark;
  }
}

//
// Tertiary headings
//

h3,
.tertiary-heading {
  color: $variables-primary-blue-dark;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 2.4rem;
  margin-bottom: rem($variables-spacing-small);

  &--is-centered {
    text-align: center;
  }

  &--is-bright {
    color: $variables-white;
    text-shadow: 0 1px 1px $variables-primary-text-shadow-color;
  }

  &--is-muted {
    color: $variables-gray-dark;
  }
}

//
// Quarternary headings
//

h4,
.quarternary-heading {
  color: $variables-primary-blue-dark;
  font-size: 1.2rem;
  font-weight: 500;
  letter-spacing: 0.15rem;
  line-height: 1.6rem;
  margin-bottom: 1.6rem;
  margin-bottom: rem($variables-spacing-small);
  margin-top: 1.6rem;
  text-transform: uppercase;

  &--is-centered {
    text-align: center;
  }

  &--is-bright {
    color: $variables-white;
    text-shadow: 0 1px 1px $variables-primary-text-shadow-color;
  }

  &--is-muted {
    color: $variables-gray-dark;
  }
}


//
// Small text
//

small,
.small-text {
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.6rem;

  &--is-centered {
    text-align: center;
  }

  &--is-bright {
    color: $variables-gray-medium;
    text-shadow: 0 1px 1px $variables-primary-text-shadow-color;
  }

  &--is-muted {
    color: $variables-gray-dark;
  }
}


//
// Paragraphs
//

p,
.paragraph {
  color: $variables-primary-blue-medium;
  font-size: rem($variables-primary-font-size);
  line-height: rem(24);
  margin-bottom: rem($variables-spacing-small);

  &--is-bright {
    color: $variables-white;
    text-shadow: 0 1px 1px $variables-primary-text-shadow-color;

    &.paragraph--is-muted {
      color: $variables-gray-dark;
    }
  }

  &--is-muted {
    color: $variables-gray-dark;
  }

  &--is-centered {
    text-align: center;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

//
// Emphasized text
//

em,
.emphasized-text {
  font-style: italic;
}

//
// Bold text
//

strong,
.strong-text {
  font-weight: 600;
}

//
// Anchors
//

a,
.anchor {
  color: $variables-primary-blue-dark;
  font-weight: 500;
  text-decoration: none;
  transition-duration: $variables-primary-easing-duration;
  transition-property: padding;
  transition-timing-function: $variables-secondary-easing;

  &--is-bright {
    color: $variables-secondary-blue-medium;

    &.anchor--is-muted {
      opacity: 0.5;
    }
  }

  &--is-muted {
    color: $variables-gray-dark;
  }
}


//
// Lists (ol, ul)
//

ul,
ol,
.unordered-list,
.ordered-list {
  color: $variables-primary-blue-medium;
  font-size: 1.6rem;
  line-height: 2.4rem;
  list-style: disc;
  margin-bottom: rem($variables-spacing-small);
  margin-top: rem($variables-spacing-small);
  padding-left: rem($variables-spacing-small);

  &--is-bright {
    color: $variables-gray-medium;
    text-shadow: 0 1px 1px $variables-primary-text-shadow-color;
  }

  // Nested unordered lists
  ul,
  ol,
  .unordered-list,
  .ordered-list {
    margin-bottom: rem($variables-spacing-small);
    margin-left: rem($variables-spacing-medium);
    margin-top: 0;

    li,
    &__list-item {
      font-size: 90%;
    }
  }

  li,
  &__list-item {
    margin-bottom: rem($variables-spacing-small);

    &:last-child {
      margin-bottom: 0;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

ol,
.ordered-list {
  list-style: decimal;
}


//
// Preformatted text
//

code,
.code {
  @include code-type()
  background-color: $variables-off-white;
  border-color: $variables-gray-light;
  border-radius: 2px;
  border-style: solid;
  border-width: 1px;
  color: $variables-primary-blue-medium;
  padding-left: rem($variables-spatial-rhythm / 2);
  padding-right: rem($variables-spatial-rhythm / 2);
}

pre,
.preformatted-text {
  @include code-type();
  background-color: $variables-off-white;
  border-color: $variables-gray-light;
  border-radius: $variables-border-radius;
  border-style: solid;
  border-width: 1px;
  color: $variables-primary-blue-medium;
  margin-bottom: rem(16);
  padding-bottom: rem($variables-spacing-large);
  padding-left: rem($variables-spacing-large);
  padding-right: rem($variables-spacing-large);
  padding-top: rem($variables-spacing-large);

  code,
  .code {
    background-color: $variables-off-white;
    border-width: 0;
    color: $variables-primary-blue-medium;
    padding-left: 0;
    padding-right: 0;
  }
}



